import React , {useState,useEffect }  from 'react';
import Layout from '../containers/common/common-layout'
import axios from 'axios';


const Profile = () => {
const [file,setFile]=useState(null);
const [user,setUser]=useState();
const[imgsrc,setImgsrc]=useState("");
   useEffect(() => {
    
    setUser(JSON.parse(localStorage.getItem("user")))
    
    
  }, []);

  useEffect(()=>{
    console.log(user)
      if (user)
    setImgsrc("images/users/"+user["firstName"]+user["lastName"]+".jpg");

  },[user])

const onInputChange = (e)=>{
    setFile(e.target.files[0])
}
const clicked=(e)=>{
    const formData= new FormData()
    formData.append('username',user["firstName"]+user["lastName"]);
    formData.append('photo',file);
    const config ={
        headers:{
            'content-type':'multipart/form-data',
        }
    }
    axios.post("http://localhost:5000/user/uploadProfileImg",formData,config).then(window.location.reload());

}
    return(
        <>
    <div>hello</div>
    <iframe src="https://drive.google.com/file/d/1qXJ5jjk8IeLiTj9eGUeSac5PSkuFMoxp/view?usp=sharing"></iframe>
    </>
)
    }


export default Profile;